.intro {
    background: linear-gradient(to right,  #A64C4C, #800000);
    color: white;
    box-shadow: 20px 20px 50px 20px #C96464;
    width: 45%;
    height: 700px;
    border: 15px #660000;
    padding: 50px;//D6F0DB
    top: 150px;
    margin-left: 500px;
    position: absolute;
    z-index: 7;
}

.intro__heading {
    margin-top: 20px;
    font-size: 3.6rem;
    font-weight: bold;
    font-style: italic;
    text-align: center;
}

.intro__name {
    font-size: 2.4rem;
    font-style: italic;
    //padding: 30px 30px 3px 30px;
    padding-top: 25px;
    padding-left: 25px; 
    width:75%;
}

.intro__title {
    font-size: 1.8rem;
    // font-weight: bold;
    line-height: 1.8rem;
    padding-top: 15px;
    padding-left: 25px; 
    padding-bottom: 15px;
    text-shadow: 1px 1px;
    width:75%;
}
.intro__image{
     padding-top: 60px;
    // padding-left: 55px;
    // width: 100px;
    // height: 150px;
    
}
.image{
    box-shadow: 20px 20px 50px 15px white;
    width: 250px;
    height: 150px;
    border-radius: 50%;
    // border-width: 2px; 
    // border-style: solid;
}
.division{
    column-count: 2;
    // width:35%;
    // padding-top: 25px;
    // padding-right: 0;
    // padding-left: 75px; 
}
.objective {
    padding-top: 70px;
    width: 100%;
    padding-left: 25px;
    padding-right: 25px; 
}

.intro__detail{
    // background-color: #A64C4C;
    color: white;
    font-size: 1.3rem;
    font-family: Arial, Helvetica, sans-serif;
    padding-top: 15px; 
    padding-bottom: 15px;
    padding-left: 20px;
    border-style: hidden;
    border-width: 0px;
    align-content: center;
    // box-shadow:20px 20px 50px white;  
    // justify-items: stretch;
}
.intro_section {
    margin-top: 95px;
}

